.projects{
  background-color: var(--background-color);
}
.container-projects {
  padding: 3rem ;
}

.slick-prev {
  position: absolute;
  left: 1%;
}

.slick-next {
  right: 1%;
}

.slick-dots {
  bottom: 2px;
}

.img-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 99%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.item-projects {
  width: 100%;
  height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
}

.img-1 {
  background-image: url(./assets/projeto\ pingpong.jpg);
}

.img-2 {
  background-image: url(./assets/patinhasUnidas.jpg);
}

.img-3 {
  background-image: url(./assets/projetostarwars.png);
}

.img-4 {
  background-image: url(./assets/git.png);
}


.item-projects h3 {
  font-size: 30px;
  color: var(--others-colors);
  position: relative;
  top: 45%;
  text-align: left;
  left: 4%;
  z-index: 2;
}

.item-projects p {
  width: 30%;
  font-size: 22px;
  color: #fff;
  position: relative;
  top: 50%;
  text-align: left;
  left: 4%;
  z-index: 2;
}

@media (max-width: 1200px) {
  .item-projects p {
      width: 60%;
  }
}

@media (max-width: 768px) {
  .container-projects {
      padding: 2rem 40px;
  }

  .item-projects p {
      width: 80%;
  }
}

@media (max-width: 460px) {
  .container-projects {
      padding: 2rem 10px;
  }

  .item-projects p {
      width: 90%;
  }

}
